@charset "utf-8";
@import "./_colorful.less";

body{
  position: relative;
  z-index: 1;
  width: 100%;
  height: 100%;
  overflow: hidden;
  &::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    background: url("../images/login_bg.png") no-repeat;
    background-position: center top;
    background-size: cover;
    background-attachment: fixed;
    filter: blur(.277778rem);
  }
}

input:-webkit-autofill {background: rgba(0, 0, 0, 0);}
input {
  display: inline-block;
  width: 100%;
  border:none;
}

.login {
  width: 100%;
  height: 100%;
  padding-top: 2.962963rem;
  .wrapper {
    padding:0 20%;
    text-align: center;
    .logo{
      width: 3.851852rem;
      height: 3.851852rem;
      border-radius: 50%;
      margin-top: .740741rem;
      // margin-bottom: 1.111111rem;
    }
    label {
      position: relative;
      display: block;
      overflow: hidden;
      padding: .185185rem .277778rem .185185rem .740741rem;
      margin-bottom: 0;
      background-color: rgba(0, 0, 0, .2);
      input {
        display: inline-block;
        height: .925926rem;
        padding-left: 0;
        border-bottom: .018519rem solid #ffffff;
        background: transparent;
        font-size: .259259rem;
        line-height: .925926rem;
        color: #ffffff;
        &::placeholder {
          color: @bg-color;
        }
      }
      &:first-of-type {
        padding-bottom: 0;
        border-radius: .074074rem .074074rem 0 0;
        background-image: url("../images/icon_user.png");
        background-repeat: no-repeat;
        background-size: .333333rem .333333rem;
        background-position: left .222222rem top .459259rem;

      }
      &:last-of-type {
        padding-top: 0;
        border-radius: 0 0 .074074rem .074074rem;
        background-image: url("../images/icon_lock.png");
        background-repeat: no-repeat;
        background-size: .444444rem .444444rem;
        background-position: left .166667rem bottom .459259rem;
        input{
          border-bottom-color: transparent;
        }
      }
    }

    .btn-login {
      display: inline-block;
      width: 100%;
      height: .925926rem;
      padding: 0;
      border:none;
      margin-top: .925926rem;
      font-size: .296296rem;
      line-height: .925926rem;
      &:hover{
        text-decoration: none;
      }
    }
  }
}
